<template>
  <div v-if="apiService">
    <pb-search v-model="filter" />
    <pb-detail-contact v-if="contact" :item="contact" />
    <pb-contacts :contacts="contacts" :view="defaultView" @filter="(q) => filter = q" />

    <div ref="observer" class="observer">
      <scale-loader
        :loading="loading"
        color="#337ab7"
        height="20px"
        width="4px"
        margin="2px"
        radius="0px"
      ></scale-loader>
    </div>
  </div>
  <div v-else class="text-center" style="margin-top: 100px">
    <h1 class="display-4">Сервис временно недоступен :-(</h1>
    <p class="lead">Загляните к нам чуть позднее. Мы его обязательно починим!</p>
  </div>
</template>

<script>
import { useMeta } from 'vue-meta'
import { config } from '@/plugins'
import { get, call } from 'vuex-pathify'
import PbSearch from '@/components/PbSearch'
import PbDetailContact from '@/components/PbDetailContact'
import PbContacts from '@/components/PbContacts'
import ScaleLoader from 'vue-spinner/src/ScaleLoader'

export default {
  name: 'ContactsView',
  components: {
    PbSearch,
    PbDetailContact,
    PbContacts,
    ScaleLoader,
  },
  data() {
    return {
      limit: 30,
      filter: this.getUrlHashValue(),
    }
  },
  mounted() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0
    }
    const callback = (entries, observer) => {
      if (entries[0].isIntersecting) {
        this.loadMoreContacts({ limit: this.contacts.length == 0 ? 10 : this.limit, filter: this.filter })
      }
    }
    const observer = new IntersectionObserver(callback, options)
    observer.observe(this.$refs.observer)
  },
  unmounted() {
    if (this.contacts.length > 100) {
      this.loadContacts({ limit: 10, filter: this.filter, reload: true })
    }
  },
  methods: {
    ...call('phonebook', [
      'loadMoreContacts',
      'loadContacts',
    ]),
    getUrlHashValue() {
      let q = decodeURIComponent(window.location.hash.slice(1))
      switch (q) {
        case "" || "index":
          return ""
      }
      return q
    },
  },
  watch: {
    filter(newValue) {
      window.location.hash = newValue
      if (newValue != this.lastFilter) {
        this.loadContacts({ limit: 10, filter: newValue, reload: true })
      }
    }
  },
  setup() {
    useMeta({
      title: config.title("Контакты"),
    })
    return {
      ...get('app/*'),
      ...get('phonebook/*'),
    }
  },
  created() {
    this.filter = this.lastFilter || this.getUrlHashValue()
    window.onhashchange = (event) => {
      let q = this.getUrlHashValue()
      switch (q) {
        case "" || "index":
          this.filter = ""
          break
        default:
          this.filter = q
      }
    }
  }
}
</script>

<style>
.observer {
  height: 30px;
}
</style>